import axios from 'axios';
import Alert from "@/plugins/Alert";
import TokenModel from "@/model/TokenModel";

const baseURL = 'https://aigc.aerofsx.com:444';

class Net {
  header = {};

  constructor(path) {
    this.apiEndpoint = baseURL + path;
    this.headers = TokenModel.Api_find_uidAndToken();
  }

  async PostFormData(params = {}) {
    //  FormData 
    const formData = new FormData();

    //  FormData
    for (const key in params) {
      if (params.hasOwnProperty(key)) {
        formData.append(key, params[key]);
      }
    }
    this.headers['Content-Type'] = 'multipart/form-data';

    const response = await axios.post(this.apiEndpoint, formData, {
      headers: this.headers,
    });
    return new Ret(response.data);
  }

  async Get(params = {}) {
    const response = await axios.get(this.apiEndpoint, {
      headers: this.headers,
      params: params, //  params 
    });

    // 
    return new Ret(response.data);
  }

}

class Ret {
  isSuccess = false;
  code = 0;
  data;
  echo = '';

  constructor(ret) {
    this.code = ret["code"];
    this.data = ret["data"];
    this.echo = ret["echo"];
    switch (ret["code"]) {
      case 0:
        this.isSuccess = true;
        break;


      case -1:
        localStorage.clear();
        Alert.SetAlert(ret["echo"])
        Alert.SetGo("/user/login")
        break;


      default:
        if (ret["code"] > 0) {
          Alert.SetAlert(ret["echo"])
        } else if (ret["code"] < 0) {
          Alert.SetAlert(ret["echo"])
        } else {

        }
        break;
    }
  }

  GetData() {
    if (!this.isSuccess) {
      return this.data;
    } else {
      return null;
    }
  }
}

export default Net;
